/*-------------------------------------------------------------------------------------------------------------------------------*/
/* ANIMATIONS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

.simple-animation {
	opacity: 0.6;
	transform: translate3d(0, 65px, 0);
	transition: all 0.8s ease-out; }
.animated .simple-animation, .simple-animation.animated {
	transform: none;
	opacity: 1; }

.decor.left.animation {
	transform: translate(-100%, 20%);
	transition: all 0.8s ease-out;
	transition-delay: 0.4s;
	&.animated {
		transform: none; } }

.decor.right.animation {
	transform: translate(100%, 20%);
	transition: all 0.8s ease-out;
	transition-delay: 0.4s;
	&.animated {
		transform: none; } }

.bg-title.animation {
	span {
		display: inline-block;
		opacity: 0;
		transform: scale(0.9);
		transition: all 0.7s ease-out;
		transition-delay: 0.2s; }
	&.animated {
		span {
			opacity: 1;
			transform: none; } } }

.popup-content {
	.decor:nth-child(1) {} }


.delay-01 {
	transition-delay: .2s; }
.delay-02 {
	transition-delay: .4s; }
.delay-03 {
	transition-delay: .6s; }
.delay-04 {
	transition-delay: .8s; }
.delay-05 {
	transition-delay: 1s; }
.delay-06 {
	transition-delay: 1.2s; }
.delay-07 {
	transition-delay: 1.4s; }

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* ELEMENTS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.video-bg:after,
.bg:after {
	content: '';
	display: block; }
.video-bg, .video-bg:after, .wr-hd, .bg, .bg:after {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0; }
.bg {
	background-size: cover;
	background-repeat: no-repeat; }
.wr-hd {
	overflow: hidden; }

.bg.parallax-bg {
	height: 125%;
	top: -10%; }

.video-bg {
	video {
		position: absolute;
		top: 50%;
		left: 50%;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%) scale(1.1); } }

.icon-arrow {
	position: relative;
	display: inline-block;
	width: 7px;
	height: 7px;
	&::before,
	&::after {
		content: "";
		position: absolute;
		top: 0;
		width: 1px;
		height: 9px;
		background-color: #111111; }
	&::before {
		left: 0;
		transform: rotate(-45deg); }
	&::after {
		right: 0;
		transform: rotate(45deg); } }



#loader-wrapper {
	position: fixed;
	left: 0;
	top: -100px;
	right: 0;
	bottom: -100px;
	background: #ffffff;
	z-index: 2000;
	animation: loader 1s linear;
	animation-iteration-count: 1;
	animation-fill-mode: forwards; }

@keyframes loader {
	0% {
		opacity: 1; }
	70% {
		opacity: 1; }
	99% {
		opacity: 0; }
	100% {
		display: none;
		visibility: hidden;
		z-index: -10; } }

